import React, { useState } from "react";
import "./styles.css";

const cropDB = {
  horticultureCrops: [
    { name: "Dragonfruit", rating: "5/5" },
    { name: "Dates", rating: "4/5" }
  ],
  agricultureCrops: [
    { name: "Groundnut", rating: "5/5" },
    { name: "Soyabean", rating: "4/5" }
  ],
  leafyGreens: [
    { name: "Spinach", rating: "5/5" },
    { name: "Kale", rating: "4/5" }
  ]
};
export default function App() {
  const [selectedCrop, setCrop] = useState("horticultureCrops");
  function cropClickHandler(crop) {
    setCrop(crop);
  }
  return (
    <div className="App">
      <h1> ðŸŒ¾ Good Crops for farming </h1>
      <p style={{ fontSize: "small" }}>
        Checkout my favorite crops for farming. Select a category to get started
      </p>
      <div>
        {Object.keys(cropDB).map((crop) => (
          <button
            onClick={() => cropClickHandler(crop)}
            style={{
              cursor: "pointer",
              background: "#E5E7EB",
              padding: "0.5rem 1rem",
              borderRadius: "0.5rem",
              border: "1px solid black",
              margin: "1rem 0.3rem"
            }}
          >
            {crop}
          </button>
        ))}
      </div>
      <hr />
      <div style={{ textAlign: "left" }}>
        <ul style={{ paddingInlineStart: "0" }}>
          {cropDB[selectedCrop].map((crop) => (
            <li
              key={crop.name}
              style={{
                listStyle: "none",
                padding: "1rem",
                border: "1px solid #D1D5DB",
                width: "70%",
                margin: "1rem 0rem",
                borderRadius: "0.5rem"
              }}
            >
              <div style={{ fontSize: "larger" }}>{crop.name}</div>
              <div style={{ fontSize: "smaller" }}>{crop.rating}</div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
